import { BASE_IMAGE_URL,getHostByName } from "@/utils/api";
import DownloadSaveFile  from "@/utils/downloadSaveFile";
import uniStatusBar from '@/components/mini_status_bar.vue';


export default {
	components: {
		uniStatusBar
	},
	data() {
		return {
			scrollHeightVal: 0,
			bannerInfoList: [],
			titleStr: '关于我们',
			name: BASE_IMAGE_URL+'name.png',
			clear: BASE_IMAGE_URL+'clear.png',
			address: '',
			ip: ''
		}
	},
	methods: {
		backAction: function(){
			uni.navigateBack();
		},
		async getHostByName(){
			if(!this.address){
				uni.showToast({
					title: '请输入域名地址'
				});
				return;
			}
			let res = await getHostByName(this.address);
			if(res.code == 1000){
				this.ip = res.data;
				// this.addr = res.data.country + res.data.city + res.data.region + res.data.isp
			}
		}
	},
	onLoad() {
		this.scrollHeightVal = this.initStatus();
	},
	onShow() {
		let that = this;
		uni.getClipboardData({
			success: function(res) {
				if(res.data){
					uni.showModal({
						title: '是否复制粘贴板内容',
						content: res.data,
						success(result) {
							if(result.confirm){
								that.address = res.data;
							};
							uni.setClipboardData({
								data: '',
								complete() {
									uni.hideToast();
								}
							})
						}
					})
				}
				
			}
		});
	}
}
